/**
 * 登入 API 端點
 * 簡化版本，直接處理登入邏輯
 */

interface Env {
    DATABASE_URL?: string
    JWT_SECRET?: string
}

interface Context {
    request: Request
    env: Env
}

// 簡單的密碼雜湊函數
async function hashPassword(password: string): Promise<string> {
    const encoder = new TextEncoder()
    const data = encoder.encode(password)
    const hashBuffer = await globalThis.crypto.subtle.digest('SHA-256', data)
    const hashArray = Array.from(new Uint8Array(hashBuffer))
    return hashArray.map(b => b.toString(16).padStart(2, '0')).join('')
}

// 驗證密碼
async function verifyPassword(password: string, hash: string): Promise<boolean> {
    // 檢查是否為 bcrypt 哈希（以 $2b$ 開頭）
    if (hash.startsWith('$2b$')) {
        // 對於 bcrypt 哈希，我們暫時使用簡化的驗證
        // 在生產環境中應該使用適當的 bcrypt 庫
        console.log('Warning: bcrypt hash detected, using fallback verification')
        return false // 暫時返回 false，需要用戶重新設置密碼
    } else {
        // 使用 SHA-256 驗證
        const passwordHash = await hashPassword(password)
        return passwordHash === hash
    }
}

// 生成簡單的 JWT token
function generateToken(userId: number, email: string, userType: string): string {
    const now = Math.floor(Date.now() / 1000)
    const expiresIn = 3600 // 1 hour
    const header = btoa(JSON.stringify({ alg: 'HS256', typ: 'JWT' }))
    const payload = btoa(
        JSON.stringify({
            userId,
            email,
            userType,
            iat: now,
            exp: now + expiresIn
        })
    )
    const signature = btoa('signature')
    return `${header}.${payload}.${signature}`
}

export async function onRequestPost(context: Context): Promise<Response> {
    const { request, env } = context

    try {
        console.log('[Auth] 處理登入請求')

        const body = (await request.json()) as any
        const { email, password } = body

        // 驗證必填欄位
        if (!email || !password) {
            return new Response(
                JSON.stringify({
                    success: false,
                    error: { code: 'VALIDATION_ERROR', message: '請填寫電子郵件和密碼' }
                }),
                {
                    status: 400,
                    headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
                }
            )
        }

        // 導入 Neon 數據庫
        const { neon } = await import('@neondatabase/serverless')
        const databaseUrl = env.DATABASE_URL
        if (!databaseUrl) {
            console.log('[Auth] DATABASE_URL 未配置')
            return new Response(
                JSON.stringify({
                    success: false,
                    error: { code: 'SERVER_ERROR', message: 'Database not configured' }
                }),
                { status: 500, headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' } }
            )
        }

        const sql = neon(databaseUrl)

        console.log('[Auth] 查詢用戶:', email)

        // 查詢用戶
        const users = await sql`SELECT * FROM users WHERE email = ${email}`
        if (users.length === 0) {
            console.log('[Auth] 用戶不存在')
            return new Response(
                JSON.stringify({
                    success: false,
                    error: { code: 'UNAUTHORIZED', message: '電子郵件或密碼不正確' }
                }),
                {
                    status: 401,
                    headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
                }
            )
        }

        const user = users[0]
        console.log('[Auth] 找到用戶:', user.email, '密碼哈希長度:', user.password_hash?.length)

        // 驗證密碼
        const isPasswordValid = await verifyPassword(password, user.password_hash)
        if (!isPasswordValid) {
            console.log('[Auth] 密碼驗證失敗')
            return new Response(
                JSON.stringify({
                    success: false,
                    error: { code: 'UNAUTHORIZED', message: '電子郵件或密碼不正確' }
                }),
                {
                    status: 401,
                    headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
                }
            )
        }

        console.log('[Auth] 登入成功，生成 token')
        const token = generateToken(user.id, user.email, user.user_type)

        return new Response(
            JSON.stringify({
                success: true,
                data: {
                    user: {
                        id: user.id,
                        email: user.email,
                        userType: user.user_type,
                        firstName: user.first_name,
                        lastName: user.last_name,
                        phone: user.phone,
                        createdAt: user.created_at,
                        updatedAt: user.updated_at,
                        isActive: user.is_active
                    },
                    tokens: {
                        accessToken: token,
                        refreshToken: '',
                        expiresIn: 3600
                    }
                }
            }),
            {
                status: 200,
                headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
            }
        )
    } catch (error: any) {
        console.error('[Auth] 登入失敗:', error)
        return new Response(
            JSON.stringify({
                success: false,
                error: { code: 'SERVER_ERROR', message: '登入失敗' }
            }),
            {
                status: 500,
                headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
            }
        )
    }
}

// 處理 OPTIONS 請求（CORS 預檢）
export async function onRequestOptions(): Promise<Response> {
    return new Response(null, {
        status: 204,
        headers: {
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE, OPTIONS',
            'Access-Control-Allow-Headers': 'Content-Type, Authorization',
            'Access-Control-Max-Age': '86400'
        }
    })
}